import template from './kidata-selector-detail.html.twig';
import './kidata-selector-detail.scss';

const { Component, Mixin } = Shopware;

Component.register('kidata-selector-detail', {
    template,

    inject: ['repositoryFactory'],

    mixins: [
        Mixin.getByName('notification')
    ],

    data() {
        return {
            isLoading: false,
            queryId: null,
            queryName: '',
            queryDescription: '',
            sql: '',
            originalPrompt: '',
            columns: [],
            rows: [],
            total: 0,
            page: 1,
            limit: 25,
            totalPages: 0,
            executed: false
        };
    },

    computed: {
        hasResults() {
            return this.rows.length > 0;
        },

        canExecute() {
            return !this.isLoading && this.sql !== '';
        },

        canExport() {
            return !this.isLoading && this.hasResults;
        }
    },

    created() {
        this.queryId = this.$route.params.id;
        this.loadQuery();
    },

    methods: {
        async loadQuery() {
            this.isLoading = true;

            try {
                const response = await fetch(`/api/_action/kidata/saved/${this.queryId}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${Shopware.Context.api.authToken.access}`
                    }
                });

                const data = await response.json();

                if (data.success) {
                    const query = data.query;
                    this.queryName = query.name;
                    this.queryDescription = query.description || '';
                    this.sql = query.sql_query;
                    this.originalPrompt = query.original_prompt || '';
                } else {
                    this.createNotificationError({
                        title: 'Fehler',
                        message: data.error || 'Query nicht gefunden'
                    });
                    this.$router.push({ name: 'kidata.selector.list' });
                }
            } catch (error) {
                this.createNotificationError({
                    title: 'Fehler',
                    message: error.message || 'An error occurred'
                });
            } finally {
                this.isLoading = false;
            }
        },

        confirmAndExecute() {
            const confirmed = confirm(
                'Sind Sie sicher, dass Sie diese SQL-Abfrage ausfhren mchten?\n\n' +
                'Sie sind selbst verantwortlich fr mgliche Performance-Probleme oder fehlerhafte Ergebnisse.'
            );

            if (confirmed) {
                this.executeQuery();
            }
        },

        async executeQuery() {
            this.isLoading = true;

            try {
                const response = await this.callApi({
                    sql: this.sql,
                    execute: true,
                    page: this.page,
                    limit: this.limit
                });

                if (response.success) {
                    this.columns = response.columns || [];
                    this.rows = response.rows || [];
                    this.total = response.total || 0;
                    this.page = response.page || 1;
                    this.totalPages = response.totalPages || 0;
                    this.executed = true;

                    this.createNotificationSuccess({
                        title: 'Erfolg',
                        message: `${this.total} Ergebnisse gefunden`
                    });
                } else {
                    this.createNotificationError({
                        title: 'Fehler',
                        message: response.error || 'Query execution failed'
                    });
                }
            } catch (error) {
                this.createNotificationError({
                    title: 'Fehler',
                    message: error.message || 'An error occurred'
                });
            } finally {
                this.isLoading = false;
            }
        },

        async changePage(params) {
            this.page = params.page || params;
            if (params.limit) {
                this.limit = params.limit;
            }
            await this.executeQuery();
        },

        async exportCSV() {
            if (!this.sql.trim()) {
                this.createNotificationError({
                    title: 'Fehler',
                    message: 'Please generate SQL first'
                });
                return;
            }

            this.isLoading = true;

            try {
                const response = await fetch('/api/_action/kidata/export', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${Shopware.Context.api.authToken.access}`
                    },
                    body: JSON.stringify({
                        sql: this.sql,
                        delimiter: ';',
                        enclosure: '"'
                    })
                });

                if (!response.ok) {
                    throw new Error('Export failed');
                }

                const blob = await response.blob();
                const url = window.URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = url;
                a.download = `${this.queryName.replace(/[^a-zA-Z0-9]/g, '_')}_${Date.now()}.csv`;
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
                window.URL.revokeObjectURL(url);

                this.createNotificationSuccess({
                    title: 'Erfolg',
                    message: 'CSV exported successfully'
                });
            } catch (error) {
                this.createNotificationError({
                    title: 'Fehler',
                    message: error.message || 'Export failed'
                });
            } finally {
                this.isLoading = false;
            }
        },

        async callApi(payload) {
            const response = await fetch('/api/_action/kidata/query', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${Shopware.Context.api.authToken.access}`
                },
                body: JSON.stringify(payload)
            });

            return await response.json();
        },

        copySQLToClipboard() {
            if (!this.sql) return;

            navigator.clipboard.writeText(this.sql).then(() => {
                this.createNotificationSuccess({
                    title: 'Erfolg',
                    message: 'SQL copied to clipboard'
                });
            });
        },

        goBackToList() {
            this.$router.push({ name: 'kidata.selector.list' });
        }
    }
});
